.header {
  position: absolute;
  z-index: 10;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: 100px;
  width: 1200px;
  margin: 0;
  padding: 0;

  @include vp-1199 {
    top: 22px;
    align-items: center;
    width: 768px;
    padding: 0 45px;
  }

  @include vp-767 {
    display: grid;
    grid-template-columns: auto max-content;
    top: 15px;
    width: 320px;
    padding: 0 15px;
  }
}

.header__logo-link picture:last-child{
  display: none;
}

.header__logo-link--opened-menu {
  picture:first-child {
    display: none;
  }

  picture:last-child {
    display: block;
  }
}

@include vp-1199 {
  .header__logo-link {
    z-index: 1;
  }
}

.header__logo-link {
  &:hover,
  &:focus-visible {
    opacity: 0.7;
    outline: none;
  }

  &:active {
    opacity: 0.5;
  }
}

.header__button-menu {
  display: none;

  @include vp-1199 {
    z-index: 1;
    display: block;
    width: 26px;
    height: 26px;
    margin: 0 101px 0 0;
    padding: 0;
    background: transparent;
    border: none;
    order: -1;
  }

  @include vp-767 {
    align-self: start;
    margin: 0;
  }
}

.header__button-menu--closed svg:last-child {
  display: none;
}

.header__button-menu--opened svg:first-child {
  display: none;
}

.header__phone {
  margin-top: 24px;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 1);
  font-size: 18px;
  font-weight: 600;
  line-height: 18px;

  @include vp-1199 {
    font-size: 16px;
    line-height: 16px;
    z-index: 1;
  }

  @include vp-767 {
    grid-column: 2  / 3;
    justify-self: end;
    margin-top: 8px;
    font-size: 14px;
    line-height: 14px;
  }
}

.header__phone--opened-menu {
  color: rgba(45, 56, 63, 1);
}

.header__phone {
  &:hover,
  &:focus-visible {
    color: rgba(28, 51, 116, 1);
    outline: none;
  }

  &:active {
    color: rgba(2, 102, 193, 1);
  }
}
